.child1 {
  padding: 20px;
}

label {
  margin-right: 10px;
}

select {
  margin: 10px 0;
}

#chart div {
  position: relative;
}

#chart .tooltip {
  position: absolute;
  background-color: white;
  border: 1px solid #ccc;
  padding: 5px;
  font-size: 12px;
  pointer-events: none;
}

#tooltip {
  position: absolute;
  pointer-events: none;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 8px; /* Rounded corners */
  padding: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow */
  display: none;
  z-index: 10; /* Ensure it appears above the chart */
}
